body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.metamask {
  width:auto;
  max-width: 200px;
  padding:4px;
  height: 25px;
  border: 1px solid #828282;
  color: black;
  border-radius: 25px;
  padding-top: 5px;
  font-size: 13px;
  text-align: center;
  margin-top: 20px !important;
  margin: auto;
  display: block; 
}
.red {
  background-color: red;
}

.parent {
   width:60%;
   height:300px;
   display: block;
   margin: auto;
   margin-top: 50px;
   background: #5332a0;
   -webkit-animation-name: fadeIn;
           animation-name: fadeIn;
   -webkit-animation-duration: 5s;
           animation-duration: 5s;
}
.child {
    position: absolute;
    margin-left:30px;
    width:30%;
    height: 50%;
    border:2px solid black;
    background: white;
    box-shadow: 2px 2px 2px 2px;
    margin-top:-40px!important;
}
.data-form {
    margin-top: 40px;
}
.data-form input {
  width:80%;
  display: block;
  margin: auto;
  border: 1px solid #5332a0;
  box-shadow: 1px 1px 1px 1px;
  margin-top:15px !important;
  font-size: 20px;
  padding: 5px;
}
.data-form button {
border: 2px solid #5332a0;
font-size: 15px;
padding:4px;
width:20%;
height:40px;
display: block;
margin: auto;
border-radius: 5px;
cursor: pointer;
}
.data-form button:hover{
 background: #cec1ee;
}
@-webkit-keyframes fadeIn {
    0% {opacity: 0;}
    100% {opacity: 1;}
 }
@keyframes fadeIn {
    0% {opacity: 0;}
    100% {opacity: 1;}
 }
